@charset "utf-8";

// ---- Bulma Customization ----

@import "~bulma/sass/utilities/_all.sass";
@import "~bulma/sass/base/_all.sass";

// Implicit Defines

$background : #FFFEF2;
$background-accent: #E0E0DA;

// Variable Overrides

$body-background-color :  $background;

@import "~bulma/bulma";

